import React from 'react'
import Layout from '../components/layout'
import trtData from '../content/trt-data.json'
import GetInTouch from '../components/get-in-touch'
import { Link } from 'gatsby'
import TRTForm from '../components/trt-form'
import Metatags from '../components/Metatags'

const EducationPage = () => {
    function PortfolioBox(title, image, imgAlt, detailsURL, description, i) {
        return (
            <Link to={detailsURL}>
                <div className="portfolio-box" key={i}>
                    <div className="portfolio-img">
                        <img src={image} alt={imgAlt} />
                    </div>
                    <h5>{title}</h5>
                    <p>{description}</p>
                </div>
            </Link>
        )
    }
    return (
        <Layout>
            <div className="trt-media-and-entertainment">
                <div className="container">
                    <div className="row justify-content-between">
                        <div className="col-md-6">
                            <h1><span>Industries We Serve</span></h1>
                            <h1>Education</h1>
                            <img src="../images/education-icon.png"
                                alt="" />
                            <h3 className="mb-3">Our Impact</h3>
                            <p>Third Rock Techkno puts great value on our clients and the work they assign us. We are committed to
                            helping them succeed and meet their business goals. We create unique, smart, innovative,
          user-centric web, mobile and voice applications for the Education industry.</p>
                            <p>Our team at Third Rock Techkno has the right knowledge, skills, and expertise to fulfill the
                            technology demands of global 21st-century education. We are fully equipped to develop business and
                            enterprise solutions for the evolving and important field of education. Our strength lies in
                            focusing on what our clients want. We develop innovative educational tools and solutions to
          strengthen e-learning and distance education.</p>
                            <h3 className="mb-3 mt-5">Our Work</h3>
                            <p className="m-0">As education and learning methods evolve, technology can be leveraged to help enhance and
                            make them available to a wider audience. Documenting precious history in digital format can secure
                            and preserve historical documentation for future generations. This is an extremely important task
          and we had the privilege of contributing to it.</p>
                            <p>The National Institute for Newman Studies (NINS) is a research institute that wanted to document its
                            books, letters, diaries in digital format so that the maximum number of users are able to access
                            them to enrich learning and knowledge. We helped NINS to build a user-friendly digital library with
          simplified access and search options.</p>
                            <div className="hire-developer-form mt-5 mb-5 mb-sm-0">
                                <h5>Hire Third Rock Techkno</h5>
                                <TRTForm />
                            </div>
                        </div>
                        <div className="col-md-4">
                            {
                                trtData.portfolioBoxData.slice(0, 3).map(({ title, image, imgAlt, detailsURL, description }, i) => PortfolioBox(title, image, imgAlt, detailsURL, description, i))
                            }
                        </div>
                    </div>
                </div>
            </div>
            <GetInTouch />
        </Layout>
    )
}

export default EducationPage